.repo--container {
  overflow-y: auto;
  height: 500px;
  margin-top: $spacer;
  &::-webkit-scrollbar{
    width: 12px;
    background-color: #F5F5F5;
  }
  &::-webkit-scrollbar-track
  {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    border-radius: 10px;
    background-color: #F5F5F5;
  }
  &::-webkit-scrollbar-thumb
  {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
    background-color: #F90;
    background-image: -webkit-linear-gradient(180deg, $primaryAppColor, $secondaryAppColor);
  }

  .repo--inner--container {
margin: $spacer *1.5 0px ;
  }

  .repo--card {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border-radius: 50px 20px 50px 0px;
    padding: 24px;
    width: 98%;
    height: 200px;
    .card--description{
      text-align: center;
      overflow-y: auto;
      &::-webkit-scrollbar{
        width: 12px;
        background-color: #F5F5F5;
      }
      &::-webkit-scrollbar-track
      {
        -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
        border-radius: 10px;
        background-color: #F5F5F5;
      }
      &::-webkit-scrollbar-thumb
      {
        border-radius: 10px;
        -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
        background-color: #F90;
        background-image: -webkit-linear-gradient(180deg, $primaryAppColor, $secondaryAppColor);
      }
    }
    a{
      text-align: center;
    }
    .card--name {
      color: #000000;
      text-align: center;
      font-family: "Ubuntu", sans-serif;
      font-weight: 700;
    }
  }
}